import React from 'react';
import { Chart } from 'react-google-charts';

export default class Table extends React.Component {
//  let options = {};
  
  constructor() {
    super();
    //this.renderChart = this.renderChart.bind(this);
  }

  componentDidMount() {

	}

	componentWillMount() {

  }
  
  render () {
    return (
    <div id="work-collections">
      <div className="col s12 m12 l6">
        <div className="row">
                    {this.renderChart()}
        </div>
      </div>
    </div>
    );
  }

  renderChart() {
    let comp = this.props.tableDados.map((enquete) => {
      console.log(enquete);
        return (
          <div className="col s12 m12 l6" key={enquete.id}>
          <h5 className="header">{enquete.pergunta}</h5>
      <div className={'my-pretty-chart-container-table' + enquete.id} key={enquete.id}>
        <Chart
          chartType="Table"
          data={enquete.valoresAgrupados}
          columns={[{"type":"string","label":"Qtd. Resposta"}]}
          graph_id={"Table" + enquete.id}
          width="100%"
          height="100%"
          legend_toggle
        />
      </div>
      </div>

    );
  });
  return comp;
  }
}



