import React from 'react';
import { Chart } from 'react-google-charts';

export default class PieChart extends React.Component {
//  let options = {};
  
  constructor() {
    super();
    //this.renderChart = this.renderChart.bind(this);
  }

  componentDidMount() {

	}

	componentWillMount() {

  }
  
  render () {
    return (
    <div id="chart-dashboard">
      <div id="card-widgets" className="seaction">
        <div className="row">
                    {this.renderChart()}
        </div>
      </div>
    </div>
    );
  }

  renderChart() {
    let comp = this.props.dados.map((enquete) => {
      console.log(enquete);
        return (
          <div className="col s12 m4 l4"  key={enquete.id}>
          <h5 className="header">{enquete.pergunta}</h5>
      <div className={'my-pretty-chart-container' + enquete.id} key={enquete.id}>
        <Chart
          chartType="PieChart"
          data={enquete.valoresAgrupados}
          options={{"title": enquete.pergunta ,"pieHole":0.4,"is3D":true}}
          graph_id={"PieChart" + enquete.id}
          width="100%"
          height="400px"
          legend_toggle
        />
      </div>
      </div>

    );
  });
  return comp;
  }
}



