import Environment from './env';

const config = {
  api: {
    enquetes: (() => `${Environment.API_BASE_URL_EXTERNAL}enquetes`)(),
    enquetesTotalizador: (() => `${Environment.API_BASE_URL_EXTERNAL}enquetes/totalizadorInicial`)(),
  },
};

export default config;



