/* @flow */

import React from 'react';
import Enquete from '../../../container/enquetes/Enquete';

class Header extends React.Component {

  render() {
    return (
            <div>

            <div id="loader-wrapper">
            <div id="loader"></div>
            <div className="loader-section section-left"></div>
            <div className="loader-section section-right"></div>
            </div>

            <header id="header" className="page-topbar">
            <div className="navbar-fixed">
                <nav className="navbar-color gradient-45deg-light-blue-cyan">
                <div className="nav-wrapper">
                    <ul className="left">
                    <li>
                        <h1 className="logo-wrapper">
                        <a href="index.html" className="brand-logo darken-1">
                            <img src="../../images/logo/materialize-logo.png" alt="materialize logo"></img>
                            <span className="logo-text hide-on-med-and-down">Enquetes DashBoard</span>
                        </a>
                        </h1>
                    </li>
                    </ul>

                </div>
                </nav>

                <Enquete />
                
              </div> 
            </header>
            </div>
    );
  }
}

export default Header;


