/* @flow */

import React from 'react';

class EnqueteComponents extends React.Component {

  render() {
   
      return (
        <nav id="horizontal-nav" className="white hide-on-med-and-down">
        <div className="nav-wrapper">
            <ul id="ul-horizontal-nav" className="left hide-on-med-and-down">              
            { this.renderComponent() }
            </ul>
        </div>       
        </nav>  
        );
    }

    renderComponent() {

        let comp = this.props.enquetes.map((enquete) => { 
        return (
                <li  key={enquete._id} className="active">
                <a className="dropdown-menu" href={'/enquete/' + enquete._id} data-activates={enquete.app}>
                <i className="material-icons">pages</i>
                <span>{enquete.app}
                    <i className="material-icons right">keyboard_arrow_down</i>
                </span>
                </a>
            </li>
            );
        });

        return comp;
    }

    renderComponentUl() {
        let comp = this.props.enquetes.map((enquete) => { 
            return (
                 <ul key={enquete._id} className="dropdown-content dropdown-horizontal-list">          
                    <li key={enquete._id} className="active"><a href="page-blank.html">Blank</a></li>
                </ul>
            );
        });
    return comp;
    }

}

export default EnqueteComponents;


